// Google Keep clone starts here

import React , { useState } from "react";
import Header from './component/Keep/Header';
import Footer from './component/Keep/Footer';
import CreateNote from './component/Keep/CreateNote';
import Note from './component/Keep/Note';
import './index.css';
import { useEffect } from "react";

const App =()=>{

  const [arr1,setArr] = useState([]);

  const addNote = (note) =>{
    localStorage.setItem("note",JSON.stringify(arr1));//So that data remains even after refreshing
    setArr((olditems)=>{
      return [...olditems,note];
    });
  }
  
  const delTheNote = (id)=>{
    setArr((olditems)=>{
      return olditems.filter((arrElem,index)=>{
        return index !== id;
      });
    });
  };

  //So that data remains even after refreshing
  useEffect(()=>{
    const data = JSON.parse(localStorage.getItem("note"));
    setArr(data);
  },[]);

  return(
    <>
    <Header/>
    <CreateNote passNote={addNote} />

    {arr1.map((val,index)=>{
      return <Note 
        id={index}
        key={index}
        title={val.title}
        content={val.para}
        deleteNote={delTheNote}
        />;
      })
    }    

    <Footer/>
    </>
  );
};

export default App;

// Google Keep clone ends here














