import React from "react";
import logo from './images/Keeps.png';
const Header = ()=>{
    return(
        <>
        <div className="header">
        <img src={logo} alt="logo" width="80" />   {/*height="80"*/} 
        <h1>For Keeps</h1>
        </div>
        </>
    );
};

export default Header;